<template>
    <tab-box-m :top-menu="topMenu" v-loading="loading"></tab-box-m>
</template>

<script>
    import TabBoxM from "../../components/layout/mobile/tabBoxM";

    export default {
        name: "distributor",
        components: {TabBoxM},
        // beforeRouteUpdate(to, from, next) {
        //     // 在当前路由改变，但是该组件被复用时调用
        //     // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
        //     // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
        //     // 可以访问组件实例 `this`
        //     this.getData(to);
        //     next();
        // },
        data() {
            return {
                loading: true,
                // routerData: {
                //     p: 1,
                //     num: 10,
                //     type: 'all',
                //     gid: 1,
                //     data: {}
                // },
                tableData:{},
                resultType:0
            }
        },
        computed: {
            topMenu: {
                get() {
                    const _self = this;
                    let topMenu = {
                        fullView:true
                    };
                    // if (_self.routerData.data.hasOwnProperty('group')) {
                    //     for (let item in _self.routerData.data.group) {
                    //         topMenu.menuList.push({
                    //             text: _self.$L(_self.routerData.data.group[item].name) + "(" + _self.routerData.data.group[item].count + ")",
                    //             router: 'distributorListMobile',
                    //             params: {
                    //                 gid: _self.routerData.data.group[item].gid,
                    //                 type: _self.routerData.type
                    //             },
                    //         });
                    //     }
                    // }
                    return topMenu;
                }
            }
        },
        created() {
            this.loading = true;
            if(this.$isMobile()){
                this.review();
            }else{
                this.$router.push({name:'distributorList'});
            }
        },
        methods: {
            action(data) {
                if (data && data.hasOwnProperty('action')) {
                    switch (data.action) {
                        case 'page':
                            this.$router.push({
                                query: {
                                    p: data.value
                                }
                            });
                            break;
                        case 'type':
                            this.$router.push({
                                params: {
                                    type: data.value
                                }
                            });
                            break;
                    }
                }
            },
            review() {
                const _self = this;
                _self.loading = true;
                _self.$api('reviewResult', {}, function (reData) {
                    if (reData.flag === 1) {
                        _self.resultType = reData.data.resultType;
                        _self.tableData = reData.data;
                        if (_self.resultType !== 1) {
                            _self.$router.push({name:'distributorAudit'});
                        }
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });

            },
        }
    }
</script>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>